  async getdefaultinvtemplate () {
    return await this.storage.get(environment.storageuniq+'defaulttemplate');
   
  }

  webdefaultinvtemplate (details) {

   return new Promise((resolve, reject) => {


           this.storage.get(environment.storageuniq+'token').then((value) => {

             this.token = value;
             let headers = new Headers();
             headers.append('Authorization', this.token);
             headers.append('Content-Type', 'application/json');

            this.http.post(this.url + '/webuser/webdefaultinvtemplate', JSON.stringify(details), {headers: headers})
              .subscribe(res => {
                let data = res.json();
                resolve(data);

              }, (err) => {
                reject(err);
              });

        });
        });


  }

